body, html {
    height: 100%;
    margin: 0;
    padding:0;
    width: 100%;
}

.todo-details {
    display: flex;
    flex-direction: column;

    header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding:.5em 1em;
    }

    .item {
        flex:1;
        padding: 1em;
    }

}

.toDoDiv {
    float: left;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 2em;

    form {

        display: flex;
        flex-direction: column;
        margin-right: 2em;

        label {
            display: flex;
            flex-direction: column;
            margin-bottom: 1em;
        }
    }
}

.listDiv{
    float: left;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 2em;
}

h2{
    padding: 20px;
    font-size: medium;
}
